// MessageBox.js

import React, {Component} from 'react';
import firebase from "firebase"
import FacebookLogin from 'react-facebook-login';
import Image5 from '../image5.jpg'


class MessageBox extends Component {

  constructor(props){
    super(props);
    this.onChange = this.onChange.bind(this);
    this.onKeyup = this.onKeyup.bind(this);
    this.state = {
      message: '',
      profile: '',
      name: ''
    };
    this.onClick = this.onClick.bind(this);
  }

  onChange(e){
    this.setState({
      message: e.target.value
    });
  }

  onClick(e) {
    var message = this.state.message
    if (message === "") {
      alert("กรุณากรอกข้อมูล")
      return false
    }
    // e.preventDefault();
    let dbCon = firebase.database().ref('/messages');
    dbCon.push({
      message: message,
      name: this.state.name,
      profile: this.state.profile,
    });
    this.setState({
      message: '',
    });
    // Swal.fire(
    //   'บันทึกข้อมูลเรียบร้อยแล้ว',
    //   'สมุยกับเจนขอบคุณมากครับ',
    //   'success'
    // )

    Swal.fire({
      title: "บันทึกข้อมูลเรียบร้อยแล้ว!",
      text: "สมุยกับเจนขอบคุณมากครับ!",
      type: "success"
    }).then(function() {
      location.reload();
    });
  }

  onKeyup(e){
    // if(e.keyCode === 13 && e.target.value !== ''){
    //   e.preventDefault();
    //   let dbCon = firebase.database().ref('/messages');
    //   dbCon.push({
    //     message: e.target.value,
    //     name: this.state.name,
    //     profile: this.state.profile,
    //   });
    //   this.setState({
    //     message: '',
    //   });
    // }
  }

  componentClicked = () => {
    console.log('click')
  }
  responseFacebook = (response) => {
      this.setState({
        profile: response.picture.data.url,
        name: response.name
      })
  }

  render() {
        return (
          <>
          {this.state.name == "" ? 
          <>
              <div className='container' style={{ marginTop: "15px" }}>
                <center>
                  <h2>ลงชื่อเข้าใช้งาน</h2>
                    <span>เพื่อร่วมอวยพร</span>
                        <div style={{ marginTop: "15px", marginBottom: "15px" }}>
                          <FacebookLogin
                          appId="1569091403558502"
                          autoLoad={false}
                          fields="name,picture"
                          onClick={this.componentClicked}
                          callback={this.responseFacebook} />
                        </div>
                        <img src={Image5} width="250px" className='myres' />
                </center>
              </div>
          </> : <>
              <div class="form-container">
                      <span>Hello, <span style={{ color: "orange"}}>{this.state.name}</span></span>
                      <h2>กรอกคำอวยพร</h2>
                      <form>
                          <textarea 
                            className="textarea"
                            placeholder="กรอกคำอวยพร..."
                            cols="100"
                            onChange={this.onChange}
                            onKeyUp={this.onKeyup}
                            value={this.state.message}
                          ></textarea>
                      </form>
                      <input onClick={this.onClick} style={{ fontFamily: "'Kanit', sans-serif", fontSize: "18px" }} type="button" value="บันทึกข้อมูล"></input>
              </div>
          </>}
          </>
        )
  }
}

export default MessageBox