import React, { Component } from "react";
import firebase from "firebase"
import _ from 'lodash';
import qrcode from "./qrcode.png";

export default class Item extends Component {
    constructor(props){
    super(props);
        this.state = {
            isPost: "",
            messages: []
        }  
    }
    componentDidMount() {
        var config = {
          apiKey: "AIzaSyCTfcRz-5lRaoXb62FYwjVf4ZE0JgYmuFY",
          authDomain: "aaaaaa-f4c65.firebaseapp.com",
          databaseURL: "https://aaaaaa-f4c65-default-rtdb.firebaseio.com",
          projectId: "aaaaaa-f4c65",
          storageBucket: "aaaaaa-f4c65.appspot.com",
          messagingSenderId: "33699669583",
        }
        firebase.initializeApp(config);
        //console.log(this.props.db.database().ref('messages'))
        let app = firebase.database().ref('messages');
        // message
        // console.log("snapshot",app)
        app.on('value', snapshot => {
          this.getData(snapshot.val());
        });

       
      }
    
      getData(values){
        let messagesVal = values;
        console.log("messagesVal",messagesVal)
        let messages = _(messagesVal).keys().map(messageKey => {
                              let cloned = _.clone(messagesVal[messageKey]);
                              cloned.key = messageKey;
                              return cloned;
                          }).value();
        this.setState({
          messages: messages
        });
      }
    render() {
      let messageNodes = this.state.messages.map((message) => {
        return (
          <li style={{ marginTop: "15px"}} >
             <img style={{ borderRadius: "50%" }} src = {message.profile} width = "60" />
             <div className="mybox">
                <h2 style={{fontWeight: "bold"}}>{message.name}</h2><hr/>
                <span style={{fontWeight: "normal", fontSize: "17px"}}>{message.message}</span> 
             </div>
          </li>
        )
      });
        return(
          <div className='card'>
            <header>
              <center>
                <h4 style={{ fontSize: "30px", marginTop: "10px", marginBottom: "10px" }}>
                  แสกนเพื่อร่วมอวยพร
                </h4>
                <img src={qrcode} width={`150px`}/>
              </center>
              <hr/>
              <div style={{ height: "790px", overflow: "hidden" }}>
                <ul id="scroll-text" style={{ listStyleType: "none" }}>
                  {messageNodes}
                </ul>
              </div>
            </header>
         
            {/* <Buttons /> */}
        
            <footer>
              {/* <a href='#'>Power By Loop's Studio</a> */}
            </footer>
        </div>
        )
    }
}