// import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";
import React, { useState, useEffect }  from "react";
// import Slider from "react-slick";
import slider1 from "./image1.jpg";
import slider2 from "./image2.jpg";
import slider3 from "./image3.jpg";
import slider4 from "./image4.jpg";
import slider5 from "./image5.jpg";
import Item from "./Item";
import MessageBox from './components/MessageBox';

import BackgroundSlider from 'react-background-slider'

const ImageSlider = () => {
  const [isPost, setIspost] = useState("");
  // Similar to componentDidMount and componentDidUpdate:
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
        const isPost = urlParams.get('is_post');
        if (isPost == "yes") {
          setIspost(isPost)
        }
  });

  return (
    <div className='App'>
      { isPost == "yes" ?
          <>
            <div className="columns">
                <div className="column is-3"></div> 
                <div className="column is-6">
                    <MessageBox />
                </div>
            </div>
          </>
          :   
          <>
            <div className='card-container'>
              <Item/>
            </div>
              <BackgroundSlider
              images={[slider1,slider2,slider3,slider4,slider5]}
              duration={5} transition={2} />
          </>
      }
    </div>
  );
};

export default ImageSlider;